import { getBanner } from '../../utils/request'
const state = {
  bannerList: [],
}

const mutations = {
  changeBannerList(state, arr) {
    state.bannerList = arr
    // console.log(state.bannerList);
  }
}

const actions = {
  bannerListActions(context) {
    getBanner().then(res => {
      context.commit('changeBannerList', res.data.list)
      // console.log(res);
    })
  },
}

const getters = {
  bannerList(state) {
    return state.bannerList
  },
}

export default {
  state,
  mutations,
  actions,
  getters,
  namespaced: true
}


